<template>
    <div>
        <p>{{questionItem.mainTitle}}</p>
        <p>{{questionItem.subTitle}}</p>
        <p>题干：{{questionItem.content}}</p>
        <p>A:{{questionItem.options[0]}}  B:{{questionItem.options[1]}}</p>
        <p>C:{{questionItem.options[2]}}  D:{{questionItem.options[3]}}</p>
        
        <p>答案：{{answer}}</p>

    </div>
</template>

<script>
//import func from 'vue-editor-bridge'
export default {
    name:'QuestionItem',
    props:[
        'questionItem',
        // mainTitle,
        // subTitle,
        // content,
        // options[3],
        // answer,
    ],
    data(){
        return{
          //answer:[],
        }
    },
    computed:{
        answer:function(){
            var a = [0,0,0,0];
            var f = [];
            var s = this.questionItem.answer;
            var i = 0;
            for(i=3;i>=0;i--){
                if(s%2 !=0 ){
                    a[i] = 1;
                }
                s = parseInt(s/2);
            }
            if(a[0]==1){
                f.push('A');
            }
            if(a[1]==1){
                f.push('B');
            }
            if(a[2]==1){
                f.push('C');
            }
            if(a[3]==1){
                f.push('D');
            }
            return f;
        }
        
    }
}
</script>